
/*Single slick*/
$(".full-slider").slick({
    dots: true,
    autoplay: false,
    autoplaySpeed: 5000,
    slidesToShow: 1,
    slidesToScroll: 1,
    navigation: true,
    adaptiveHeight: true,
    infinite: true
});
/* matcheght */
$('.avil-webinars-slider .slide').matchHeight();
$('.post-summary').matchHeight();

/* === Slider with filter */
var slickoptions = {
    rows: 1,
    dots: true,
    appendDots: $('.slick-nav'),
    appendArrows: $('.slick-nav'),
    accessibility: true,
    speed: 300,
    slidesToShow: 3,
    slidesToScroll: 3,
    infinite: false,
    adaptiveHeight: true,
    responsive: [
        {
            breakpoint: 991,
            settings: {
                slidesToScroll: 1,
                slidesToShow: 2,
                dots: true
            }
        },
        {
            breakpoint: 768,
            settings: {
                slidesToScroll: 1,
                slidesToShow: 2,
                dots: true
            }
        },
        {
            breakpoint: 650,
            settings: {
                slidesPerRow: 1,
                slidesToScroll: 1,
                slidesToShow: 1,
                dots: true
            }
        }
    ]
}

$(document).ready(function () {
    $('.avil-webinars-slider').slick(slickoptions);

    var slider = $('.avil-webinars-slider');
    var allSlides = $('.avil-webinars-slider-wrapper .slick-slide > div > *').clone();
    var trigger = $('js-filter');
    var ClassFilter = function (object, item) {
        this.object = object;
        this.item = item;
        this.filterFunc = function () {
            $('.active').removeClass('active');
            $('.js-filter[data-filter="' + this.item + '"]').addClass('active');
            var filterSlides = allSlides.filter(this.item);

            slider.css({ "opacity": "0", "left": "50px" });
            setTimeout(
                function () {
                    slider
                        .slick('unslick')
                        .empty()
                        .append(filterSlides)
                        .slick(slickoptions)
                        .css({ "opacity": "100", "left": "0px" })
                }, 600);
        }
    }

    $('.filters-button-group .button').click(function () {
        var filterDataheading = $(this).attr('data-filter');
        $('.filter-subheading-holder .filter_subheading[data-fil="' + filterDataheading + '"]').show();
        $('.filter-subheading-holder .filter_subheading[data-fil="' + filterDataheading + '"]').siblings().hide();

        setTimeout(() => {
            $('.avil-webinars-slider .slide').matchHeight();
            $('.post-summary').matchHeight();
        }, 700);
        
    })

    jQuery('.js-filter').on('click', function (e) {
        var attr = jQuery(this).attr('data-filter')
        var newFilter = new ClassFilter(this, attr);
        newFilter.filterFunc();
    });

    $(".speakers_gallery").slick({
        dots: true,
        autoplay: true,
        autoplaySpeed: 5000,
        slidesToShow: 3,
        slidesToScroll: 1,
        navigation: true,
        infinite: true,
        responsive: [
            {
                breakpoint: 991,
                settings: {
                    slidesToScroll: 1,
                    slidesToShow: 2,
                    dots: true
                }
            },
            {
                breakpoint: 768,
                settings: {
                    slidesToScroll: 1,
                    slidesToShow: 2,
                    dots: true
                }
            },
            {
                breakpoint: 575,
                settings: {
                    slidesPerRow: 1,
                    slidesToScroll: 1,
                    slidesToShow: 1,
                    dots: true
                }
            }
        ]
    });

    $(".trending_webinar_slider").slick({
        dots: true,
        autoplay: false,
        slidesToShow: 1,
        slidesToScroll: 1,
        navigation: true,
        infinite: true,
        fade: true,
        speed: 1200,
        centerMode: true,
        adaptiveHeight: true,
    });



    $(".related_slider_group").slick({
        dots: true,
        autoplay: true,
        autoplaySpeed: 5000,
        slidesToShow: 3,
        slidesToScroll: 1,
        navigation: true,
        infinite: true,
        responsive: [
            {
                breakpoint: 1400,
                settings: {
                    slidesToScroll: 1,
                    slidesToShow: 2,
                    dots: true
                }
            },
            {
                breakpoint: 991,
                settings: {
                    slidesPerRow: 1,
                    slidesToScroll: 1,
                    slidesToShow: 1,
                    adaptiveHeight: true
                }
            }
        ]
    });

    $('.hamburger_icon').click(function () {
        $('#header_nav').slideToggle(300);
    });

    $('.search-icon').click(function (e) {
        e.stopPropagation();
        $('body').addClass('search-show');
        $('.search-wrapper input#search').focus();
        $('body').addClass('set-perspective');
    });
    $('body').click(function () {
        $('body').removeClass('search-show');
        setTimeout(() => {
            $('body').removeClass('set-perspective');
        }, 500);
    });
    $('.search-wrapper').detach().appendTo('body');
    $('.search-wrapper form').click(function (e) {
        e.stopPropagation();
    });

    $(window).scroll(function () {
        if ($(window).scrollTop() > 100) {
            $('body').removeClass('search-show');
            setTimeout(() => {
                $('body').removeClass('set-perspective');
            }, 500);
        }
    });
    /* filter */
    $('.sidebar-filter-btn').click(function (e) {
        e.preventDefault();
        $('body').toggleClass('filter-show');
    });
    $('.close-sidebar').click(function (e) {
        e.preventDefault();
        $('body').removeClass('filter-show');
    });
    $('.short-btn').click(function () {
        $(this).toggleClass('active');
        $(this).next('ul').fadeToggle(200);
    });
    $('.short-filter ul a').click(function(){
        $('.short-filter ul').fadeOut();
        $('.short-btn').removeClass('active');
    });

    /* magicline */
    $('nav.header_nav > ul').append('<div class="magicBorder"></div>');
    $('.topic-filter ul').append('<div class="magicBorderClick"></div>');
    function magicborder() {
        $('.magicBorder').css({
            'width': $('nav.header_nav > ul > li:first-child').width() + 40,
            'left': $('nav.header_nav > ul > li:first-child').position().left,
        });

        $('nav.header_nav > ul > li:not(.no-magic)').hover(function () {
            var magicWidth = $(this).width() + 40;
            var magicPos = $(this).position().left;
            $('.magicBorder').css({
                'width': magicWidth,
                'left': magicPos,
            });
        }, function () {
            $('.magicBorder').css({
                'width': $('nav.header_nav > ul > li:first-child').width() + 40,
                'left': $('nav.header_nav > ul > li:first-child').position().left,
            });
        });

        /* ======= magic border onclick ========= */
        let magiCLine = document.querySelector('.magicBorderClick')
        if(magiCLine) {
            $('.magicBorderClick').css({
                'width': $('.content_tab_nav li.active').width(),
                'left': $('.content_tab_nav li.active').position().left,
            });

            $('.content_tab_nav a').click(function (e) {
                $(this).parent().addClass('active');
                $(this).parent().siblings('li').removeClass('active');
                e.preventDefault();
                var magicWidth2 = $(this).parent().width();
                var magicPos2 = $(this).parent().position().left;
                $('.magicBorderClick').css({
                    'width': magicWidth2,
                    'left': magicPos2,
                });
            });
        }
    }
    $(document).ready(function () {
        magicborder();
    });
    $(window).on('resize', magicborder);

    $('.remove-cat').click(function () {
        $(this).parent().remove();
    });

    function hasSelectfil() {
        if ($('.selected-cat').length < 1) {
            $('.no-selected-val').show();
        }
    }
    hasSelectfil();
    $('.remove-cat').click(function () {
        hasSelectfil();
    });



    // $('.filter-search-trigger').click(function(e){
    //     e.preventDefault();
    //     $('.filter-search').toggleClass('filter-search-show');
    //     $('.filter-search .form-group input').focus();
    // });

    $('.cancel-btn').click(function () {
        $('body').removeClass('filter-show');
    })

    $('.filter-widget-toggle').click(function () {
        $(this).parent().next('.filter-widget').slideToggle(300);
        $(this).toggleClass('active');
    });

    var swiperContainer = document.querySelector('.swiper-container');
    if (swiperContainer) {
        var swiper = new Swiper(".swiper-container", {
            effect: "coverflow",
            grabCursor: true,
            centeredSlides: true,
            slidesPerView: "auto",
            coverflowEffect: {
                rotate: 20,
                stretch: 0,
                depth: 350,
                modifier: 1,
                slideShadows: true
            },
            navigation: {
                nextEl: '.swiper-button-next',
                prevEl: '.swiper-button-prev',
            },
            pagination: {
                el: ".swiper-pagination"
            }
        });
    }
    /* tabber */
    $('.content_tab_nav li a').click(function (e) {
        e.preventDefault();
        $(this).parent().addClass('active');
        $(this).parent().siblings().children('a').removeClass('.active');
        var tabId = $(this).attr('href');
        $(tabId).addClass('active_tab');
        $(tabId).siblings('.tab_content_item').removeClass('active_tab');
    });

    $('.grid').isotope({
        itemSelector: '.grid-item',
        percentPosition: true,
        masonry: {
            columnWidth: '.grid-sizer'
        }
    });
    $('.sidebar-filter-btn, .close-sidebar').click(function (e) {
        $('.grid').isotope({
            itemSelector: '.grid-item',
            percentPosition: true,
        });
    });
    

    /* === Speaker modal js */
    // do not chnage the positon of this code
    $('.speakers .speaker-column').each(function(i){
        $(this).attr('data-speaker','speakerpop'+i);
        $(this).find('.speaker-modal').attr('id','speakerpop'+i);
        $(this).find('.member-image img').clone().appendTo($(this).find('.speaker-modal .speaker-modal-img'));
        $(this).find('.member-details').clone().insertAfter($(this).find('.speaker-modal .speaker-modal-img'));
    });
    $('.speaker-modal').detach().insertAfter('body');
    $('.speaker-column').click(function(){
        var speakerPopId = $(this).attr('data-speaker');
        $('.speaker-modal[id="'+speakerPopId+'"]').addClass('show'); 
    });
    $('.close-modal, .speaker-modal').click(function(){
        $('.speaker-modal').removeClass('show'); 
    });
    $('.speaker-inner').click(function(e){
        e.stopPropagation();
    });

    $('.related_image_card').matchHeight();
    $('.related_image_card .sub_title').matchHeight();
    
});






